.page-card__box-title {
  display: flex;
  align-items: flex-start;
}
.page-card__box-title:not(:last-child) {
  margin-bottom: 30px;
}
.page-card__title {
  font-size: clamp(1.25rem, 0.943rem + 1.31vw, 2rem);
}
.page-card__favorite {
  width: 38px;
  height: 38px;
  background: url("../images/icons/favorite1.svg") left center/cover no-repeat;
}
.page-card__price {
  letter-spacing: 0.04em;
}
.page-card__price-name {
  display: inline-block;
  margin-right: 10px;
  font-size: 14px;
  text-transform: capitalize;
  color: #c5c5c5;
}
.page-card__price-descr {
  display: inline-block;
  font-size: 14px;
}
.page-card__price-price {
  display: inline-block;
  font-weight: 500;
  font-size: 28px;
  letter-spacing: -0.05em;
  color: #1d1d1d;
}
.page-card__peculiarities {
  margin: 24px 0;
  display: flex;
  align-items: center;
  gap: 9px;
  font-weight: 600;
  font-size: 12px;
  line-height: 100%;
  text-transform: uppercase;
  color: var(--white);
  text-transform: uppercase;
}
.page-card__peculiarities-sale {
  background-color: var(--red);
  padding: 6px 8px;
}
.page-card__peculiarities-new {
  background-color: var(--green);
  padding: 6px 8px;
}
.page-card__accordeon {
  margin-top: 20px;
}

.quantity {
  display: inline-block;
}

input[type=number]::-webkit-inner-spin-button,
input[type=number]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type=number] {
  -moz-appearance: textfield;
}

.quantity input {
  width: 49px;
  height: 14px;
  display: block;
  border: none;
  background-color: transparent;
  font-weight: 500;
  line-height: 100%;
  color: var(--main-color);
}

.quantity input:focus {
  outline: 0;
}

.quantity-nav {
  float: left;
  position: static;
  height: 42px;
}

.quantity-button {
  cursor: pointer;
  width: 1.25rem;
  height: 1.25rem;
  display: flex;
  justify-content: center;
  align-items: center;
  top: 0;
  outline: 0.0625rem solid red;
}

.catalog__list {
  display: grid;
  grid-template-columns: repeat(auto-fit, 300px);
  gap: 1.875rem;
  grid-column: 4 span;
  justify-content: center;
  justify-items: center;
  margin: 0 auto;
}
@media screen and (width < 661.99px) {
  .catalog__list {
    grid-template-columns: repeat(2, 162px);
    grid-template-rows: repeat(2, 380px);
  }
}

.form-size {
  margin: 20px 0;
}
.form-size__select {
  padding: 14px 11px;
  border: 1px solid #c5c5c5;
  border-radius: 37px;
  width: 203px;
  height: 47px;
  font-size: 14px;
  line-height: 130%;
  letter-spacing: 0.04em;
  background: #fff;
}

.descr-product:not(:last-child) {
  margin-bottom: 24px;
}
.descr-product__name {
  display: inline-block;
  width: 156px;
  font-weight: 600;
  line-height: 100%;
  text-transform: uppercase;
}
.descr-product__text {
  display: inline-block;
  line-height: 130%;
  letter-spacing: 0.04em;
}

.filters {
  display: none;
  box-sizing: content-box;
  animation: opac 0.7s;
  position: absolute;
  z-index: 7;
  top: 35px;
  right: 5px;
  padding: 2rem 2rem 2rem 4.5rem;
  border-radius: 1rem;
  box-shadow: 0 0 15px 0 rgba(0, 0, 0, 0.2);
  width: 343px;
  background-color: var(--white);
  transition-duration: 550ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}
.filters__btn {
  width: 100%;
  display: flex;
  align-items: center;
  gap: 16px;
  margin-bottom: 30px;
  cursor: pointer;
  transition-duration: 550ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  font-weight: 600;
  line-height: 1;
  text-transform: uppercase;
}
.filters__btn-arrow {
  transition-duration: 550ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}
.filters__btn.active .filters__btn-arrow {
  transform: rotate(180deg);
}
.filters-form__content--size .jq-checkbox,
.filters-form__content--size .jq-radio {
  margin: 0 5px 0 0;
}
.filters-form__content.flex {
  gap: 25px;
  justify-content: center;
}
.filters-form__drop {
  margin: 30px 0;
}
.filters-form__label {
  display: flex;
  font-size: 14px;
  line-height: 1.3;
  letter-spacing: 0.04em;
  cursor: pointer;
}
.filters-form__label--red {
  color: var(--red);
}
.filters-form__label-quantity {
  display: inline-block;
  margin-left: auto;
  color: var(--main-color);
}
.filters-form__label:not(:last-child) {
  margin-bottom: 21px;
}

.filters.active {
  display: block;
}

@keyframes opac {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.accordeon__item {
  padding: 30px 0;
  border-top: 1px solid #e4e4e4;
}
.accordeon__title {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-weight: 600;
  line-height: 100%;
  text-transform: uppercase;
  text-align: left;
  cursor: pointer;
}
.accordeon__title::after {
  content: url("../images/icons/arrow-up-accordeon.svg");
  transition-duration: 550ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}
.accordeon__text {
  display: none;
  margin-top: 30px;
}
.accordeon__text-list {
  margin-top: 30px;
}
.accordeon__text-item:not(:last-child) {
  margin-bottom: 16px;
}
.accordeon__text-item::marker {
  font-size: 0;
}
.accordeon__title.title--active::after {
  content: url("../images/icons/arrow-down-accordeon.svg");
}
.accordeon__link {
  width: 260px;
}
.accordeon__link--courier {
  padding-left: 48px;
  background: url(../images/icons/courier.svg) left center/32px 32px no-repeat;
}
.accordeon__link--points {
  padding-left: 48px;
  background: url(../images/icons/points.svg) left center/32px 32px no-repeat;
}
.accordeon__link--pickup {
  padding-left: 48px;
  background: url(../images/icons/pickup.svg) left center/32px 32px no-repeat;
}

.profile {
  padding-top: 52px;
}
.profile__container {
  max-width: 1082px;
}
.profile__title {
  text-align: center;
}
.profile__list {
  padding-top: 60px;
  padding-bottom: 44px;
  display: flex;
  justify-content: center;
  gap: calc(var(--valeus-gap) * 2);
}
.profile__item {
  position: relative;
  text-align: center;
}
.profile__item:not(:last-child)::after {
  position: absolute;
  content: "";
  top: 0;
  right: calc(-1 * var(--valeus-gap));
  width: 1px;
  height: 100%;
  background-color: red;
}
.profile__head {
  line-height: 130%;
  letter-spacing: 0.04em;
}
.profile__head:not(:last-child) {
  margin-bottom: 15px;
}
.profile__meaning {
  font-weight: 600;
  line-height: 100%;
  text-transform: uppercase;
  color: #1d1d1d;
}

.personal-data__box {
  padding-top: 85px;
  border-top: 1px solid #dededb;
}
.personal-data__nav {
  grid-column: 2 span;
  padding-top: 7px;
}
.personal-data__nav-item {
  padding-left: 16px;
}
.personal-data__nav-item:not(:last-child) {
  margin-bottom: 15px;
}
.personal-data__nav-btn {
  font-weight: 500;
  line-height: 100%;
  text-transform: uppercase;
}
.personal-data__content {
  grid-column-start: 4;
  grid-column-end: 11;
}
.personal-data__content-item {
  position: relative;
  padding-bottom: calc(var(--form-gap) * 2);
}
.personal-data__content-item::after {
  position: absolute;
  content: "";
  left: 0;
  bottom: var(--form-gap);
  width: 100%;
  height: 1px;
  background-color: #dededb;
}
.personal-data .personal-data__nav-btn.mixitup-control-active .personal-data__nav-btn::before {
  content: "";
  width: 5px;
  height: 5px;
  border-radius: 100%;
  background-color: var(--main-color);
}

.person-form__title {
  text-transform: none;
}
.person-form__title:not(:last-child) {
  margin-bottom: 24px;
}
.person-form__info {
  padding-left: 20px;
  width: 100%;
  display: flex;
  align-items: center;
  gap: 16px;
  font-weight: 500;
  line-height: 130%;
  letter-spacing: 0.04em;
  color: var(--blue);
  background: rgba(47, 128, 237, 0.2);
  height: 52px;
}
.person-form__info:not(:last-child) {
  margin-bottom: 32px;
}
.person-form__label {
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 10px;
  font-weight: 500;
  line-height: 100%;
  text-transform: uppercase;
  color: var(--black);
}
.person-form__label:not(:last-child) {
  margin-bottom: 18px;
}
.person-form__label-necessarily::after {
  content: "*";
  color: var(--red);
}
.person-form__input {
  display: block;
  width: 100%;
  border: 1px solid #c5c5c5;
  border-radius: 50px;
  background-color: var(--white);
  padding: 15px;
  color: var(--main-color);
}
.person-form .checkbox {
  position: absolute;
  opacity: 0;
  z-index: -1;
}
.person-form .checkbox__label {
  display: flex;
  align-items: center;
  gap: 16px;
  cursor: pointer;
}
.person-form .checkbox__label:not(:last-child) {
  margin-bottom: 28px;
}
.person-form .checkbox__castom-icon {
  cursor: pointer;
  width: 20px;
  height: 20px;
  border-radius: 100%;
  border: 1px solid #c5c5c5;
  background-color: transparent;
  transition-duration: 550ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}
.person-form .checkbox__icon-check {
  background-color: transparent;
  transition-duration: 550ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}
.person-form .checkbox:checked + .checkbox__label .checkbox__castom-icon {
  background-color: #c5c5c5;
}
.person-form .checkbox:checked + .checkbox__label .checkbox__castom-icon .checkbox__icon-check {
  background-color: #c5c5c5;
}
.person-form--delivery .person-form__info {
  color: var(--main-color);
  background-color: rgba(233, 233, 233, 0.4);
}
.person-form--delivery .person-form__info svg {
  color: inherit;
}
.person-form--delivery .person-form__label {
  display: inline-flex;
  width: calc((100% - 16px) / 2);
}
.person-form--delivery .person-form__label:nth-child(even) {
  margin-right: 12px;
}
.person-form--delivery .person-form__button {
  display: inline-flex;
}
.person-form--delivery .person-form__button:not(:last-child) {
  margin-right: 16px;
}

.button {
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50px;
  padding: 16px 24px;
  height: 46px;
  font-weight: 600;
  line-height: 100%;
  text-transform: uppercase;
  border: 1px solid var(--main-color);
  transition-duration: 550ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}
.button--bg-main-color {
  background-color: var(--main-color);
  color: var(--white);
}
@media (any-hover: hover) {
  .button--bg-main-color:hover {
    background-color: var(--white);
    color: var(--main-color);
  }
}
.button--bg-white {
  background-color: var(--white);
  color: var(--main-color);
}
@media (any-hover: hover) {
  .button--bg-white:hover {
    background-color: var(--main-color);
    color: var(--white);
  }
}